@import"https://fonts.googleapis.com/css2?family=Inter:wght@400;600;700&display=swap";.about-container{padding:2rem;margin:auto;font-family:"Inter",sans-serif;background:linear-gradient(135deg, #0f2027, #203a43, #2c5364);color:#f1f1f1;background:linear-gradient(135deg, #0f172a, #1e293b)}.about-container section{background:hsla(0,0%,100%,.06);border-left:6px solid #1ab9ca;border-radius:5px;padding:2.5rem;margin-bottom:2rem;backdrop-filter:blur(12px);box-shadow:0 12px 32px rgba(0,0,0,.3);transition:transform .3s ease,box-shadow .3s ease}.about-container section:hover{transform:translateY(-10px);box-shadow:0 20px 40px rgba(72,187,255,.3)}.about-container h2,.about-container h3{font-weight:700;font-size:2rem;margin-bottom:1.2rem;color:#f9d923;text-shadow:1px 1px 2px rgba(0,0,0,.6);background:linear-gradient(to right, #f9d923, #fcca46);-webkit-background-clip:text;-webkit-text-fill-color:rgba(0,0,0,0)}.about-container p{font-size:1.05rem;color:#e4e4e4;line-height:1.6;margin-bottom:1.2rem}.about-container ul{list-style:none;padding:0;margin-top:1.5rem}.about-container ul li{display:flex;align-items:flex-start;font-weight:500;color:#fcca46;position:relative;padding-left:2rem;margin-bottom:1rem;transition:color .3s ease}.about-container ul li::before{content:"✔";color:#38b000;position:absolute;left:0;top:0;font-size:1.2rem;font-weight:bold}.about-container ul li:hover{color:#fff}@media(max-width: 768px){.about-container section{padding:1.8rem}.about-container h2,.about-container h3{font-size:1.5rem}.about-container ul li,.about-container p{font-size:.95rem}}