.itr-container{padding:3rem 2rem;background-color:#f7f9fc;background:#879099;font-family:"Segoe UI",sans-serif;color:#333;background:linear-gradient(135deg, #0f172a, #1e293b)}.itr-section{display:flex;justify-content:space-between;align-items:flex-start;gap:2rem;flex-wrap:wrap;margin-bottom:4rem}.itr-left{flex:1}.itr-left h1{font-size:2.5rem;color:#0a4f70;color:#fff;margin-bottom:1rem}.itr-left h3{font-size:1.4rem;color:#20c3ad;margin-bottom:1rem}.itr-left p{font-size:1.05rem;line-height:1.8;margin-bottom:1.5rem;color:#0a4f70}.itr-left .call-btn{display:inline-block;background:linear-gradient(135deg, #007bff, #00bcd4);color:#fff;padding:.8rem 1.7rem;border-radius:30px;text-decoration:none;justify-content:center;display:flex;text-align:center;font-weight:600;box-shadow:0 6px 15px rgba(0,123,255,.2);transition:all .3s ease-in-out}.itr-left .call-btn:hover{background:linear-gradient(135deg, rgb(0, 98.4, 204), rgb(0, 142.7735849057, 161));transform:scale(1.05)}.itr-right{flex:1;background:lab(60.79 -26.75 -19.45);color:#ddd;padding:2.2rem;border-radius:14px;box-shadow:0 8px 30px rgba(0,0,0,.08)}.itr-right h2{font-size:1.7rem;margin-bottom:1rem;color:#0a4f70}.itr-right p{margin-bottom:1.5rem;font-size:1rem;color:#fdfbfb}.itr-right form{display:flex;flex-direction:column;gap:1.1rem}.itr-right form input,.itr-right form select{padding:.85rem;border:1px solid #ddd;border-radius:8px;font-size:1rem;transition:.2s ease}.itr-right form input:focus,.itr-right form select:focus{border-color:#007bff;outline:none}.itr-right form .submit-btn{background:linear-gradient(135deg, #ff5722, #ff9800);color:#fff;padding:.8rem 1.2rem;border:none;border-radius:30px;font-weight:600;cursor:pointer;box-shadow:0 6px 18px rgba(255,87,34,.3);transition:all .3s ease-in-out}.itr-right form .submit-btn:hover{background:linear-gradient(135deg, rgb(238, 57.0769230769, 0), rgb(204, 121.6, 0));transform:translateY(-2px)}.itr-right .privacy-text{font-size:.85rem;margin-top:1rem;color:#777;text-align:center}.itr-heading{text-align:center;font-size:2rem;color:#007bff;color:#e6e60c;margin-bottom:2.5rem}.itr-heading span{color:#ff5722;color:#fff}.itr-grid{display:grid;grid-template-columns:repeat(auto-fit, minmax(220px, 1fr));gap:1.5rem;margin-bottom:4rem}.itr-card{background:#fff;background:#081214;border-radius:14px;padding:1.5rem;text-align:center;box-shadow:0 6px 16px rgba(0,0,0,.06);transition:all .3s ease-in-out}.itr-card:hover{transform:translateY(-6px);box-shadow:0 12px 24px rgba(0,0,0,.1)}.itr-card .itr-icon{font-size:2.5rem;color:#007bff;color:wheat;margin-bottom:.8rem}.itr-card .itr-title{font-weight:700;margin-bottom:.5rem;font-size:1.15rem;color:#fff}.itr-card .itr-description{font-size:1rem;color:#077e82}.why-choose-section1{background:#081214;padding:2.5rem;border-radius:14px;margin-bottom:3rem}.why-choose-section1 h2{text-align:center;margin-bottom:2rem;color:#f6f7f8;font-size:1.8rem}.why-choose-section1 .reasons-grid{display:grid;grid-template-columns:repeat(auto-fit, minmax(240px, 1fr));gap:1.5rem}.why-choose-section1 .reasons-grid .reason-card{background:#181717;padding:1.2rem;border-radius:10px;box-shadow:0 5px 15px rgba(0,0,0,.05);font-size:1rem;color:#077e82;transition:.3s}.why-choose-section1 .reasons-grid .reason-card:hover{transform:translateY(-4px)}.who-should-file{background-color:hsla(0,0%,100%,.4);padding:2rem 0;background:#081214}.who-should-file h2{font-size:1.7rem;color:#0a4f70;text-align:center;margin-bottom:1rem}.who-should-file p{text-align:center;max-width:800px;margin:0 auto;color:#077e82;font-size:1.05rem}.itr-types{margin:3rem 0;background:#081214}.itr-types h2{text-align:center;color:#fff;margin-bottom:2rem;font-size:1.8rem}.itr-types .itr-cards{display:grid;grid-template-columns:repeat(auto-fit, minmax(260px, 1fr));gap:1.5rem}.itr-types .itr-cards .card{background:#081214;padding:1.7rem;border-radius:12px;box-shadow:0 0 16px rgba(0,0,0,.07)}.itr-types .itr-cards .card h3{color:wheat;text-align:center;margin-bottom:.7rem;font-size:1.3rem}.itr-types .itr-cards .card p{font-size:.95rem;color:#077e82}.how-we-help-card{padding:4rem 2rem;background:#081214;text-align:center}.how-we-help-card h2{font-size:2.5rem;color:#fff;margin-bottom:2rem;font-weight:700}.how-we-help-card .card{background:#101010;max-width:700px;margin:0 auto;padding:2.5rem;border-radius:1.5rem;box-shadow:0 6px 20px rgba(0,0,0,.1);transition:all .3s ease}.how-we-help-card .card p{font-size:1.1rem;color:#89871b;margin-bottom:1.5rem;font-weight:500}.how-we-help-card .card ol{text-align:left;padding-left:1.5rem;color:#1e293b;font-size:1.05rem;line-height:1.7}.how-we-help-card .card ol li{margin-bottom:.75rem;position:relative;padding-left:1.5rem;list-style:none;color:#077e82}.how-we-help-card .card ol li::before{content:"✔️";position:absolute;left:0;top:0;color:#10b981;font-size:1rem}.how-we-help-card .card .btn-contact{margin-top:2rem;padding:.75rem 1.75rem;background:linear-gradient(to right, #14b8a6, #0ea5e9);color:#fff;font-weight:600;font-size:1rem;border:none;border-radius:30px;cursor:pointer;transition:transform .2s ease,background .3s}.how-we-help-card .card .btn-contact:hover{background:linear-gradient(to right, #0ea5e9, #14b8a6);transform:translateY(-3px);box-shadow:0 4px 15px rgba(14,165,233,.4)}.documents-cards{background:#081214;padding:2.5rem 0}.documents-cards h2{text-align:center;color:#0a4f70;margin-bottom:1.5rem}.documents-cards .card{max-width:600px;margin:0 auto;background:#fff;background:#0f1415;padding:1.8rem;border-radius:14px;box-shadow:0 0 15px rgba(0,0,0,.06)}.documents-cards .card ul{list-style:disc;padding-left:1.5rem}.documents-cards .card ul li{font-size:1rem;margin-bottom:.5rem;color:#077e82}@media(max-width: 768px){.itr-section{flex-direction:column}.itr-left,.itr-right{width:auto}.itr-heading{font-size:1.6rem}.itr-right form input,.itr-right form select{font-size:.95rem}}@media(max-width: 480px){.itr-heading{font-size:1.4rem}.itr-card .itr-title{font-size:1rem}.itr-card .itr-description{font-size:.9rem}.itr-right{padding:1.4rem}}