.trademark-container{padding:2rem;font-family:"Segoe UI",sans-serif;background:#879099;color:#222}.trademark-container h1{text-align:center;color:#fff;font-size:2.5rem;margin-bottom:2rem}.trademark-container section{margin-bottom:2.5rem;background:#39707b;padding:2rem;border-radius:1.5rem;box-shadow:0 6px 18px rgba(0,0,0,.08)}.trademark-container h2{color:#101010;margin-bottom:1rem;font-size:1.6rem}.trademark-container ul{list-style:none;padding-left:0}.trademark-container ul li{position:relative;padding-left:1.5rem;margin-bottom:.7rem;font-size:1rem}.trademark-container ul li::before{content:"✔";position:absolute;left:0;color:#0ceb13;font-weight:bold}.trademark-container .form-trademark-wrapper{display:flex;justify-content:space-between;gap:2rem;margin-bottom:2.5rem}.trademark-container .form-trademark-wrapper .form-section,.trademark-container .form-trademark-wrapper .why-trademark{flex:1;padding:2rem;border-radius:1.2rem;box-shadow:0 6px 18px rgba(0,0,0,.08)}.trademark-container .form-trademark-wrapper form{display:flex;flex-direction:column;gap:1rem}.trademark-container .form-trademark-wrapper form input{padding:.9rem 1rem;border-radius:8px;border:1px solid #a7a6a6;color:#fff;font-size:1rem;transition:.2s;background:#39707b}.trademark-container .form-trademark-wrapper form input:focus{border-color:#4caf50;box-shadow:0 0 6px rgba(76,175,80,.3);outline:none}.trademark-container .form-trademark-wrapper form button{background:linear-gradient(45deg, #075a60, #018808);border:none;border-radius:8px;padding:.9rem;font-size:1rem;color:#fff;cursor:pointer;transition:.3s ease}.trademark-container .form-trademark-wrapper form button:hover{background:linear-gradient(45deg, #cbd116, #767934)}.trademark-container .pricing-docs{display:flex;flex-wrap:wrap;gap:2rem}.trademark-container .pricing-docs .pricing,.trademark-container .pricing-docs .documents{flex:1;background:linear-gradient(135deg, #879099, #39707b);padding:1.5rem;border-radius:1rem;box-shadow:0 4px 12px rgba(0,0,0,.06)}.trademark-container .pricing-docs .pricing p,.trademark-container .pricing-docs .documents p{margin-bottom:.5rem;font-size:1rem}.trademark-container .steps{display:flex;flex-wrap:wrap;gap:1.5rem}.trademark-container .steps .step-card{flex:1;background:linear-gradient(135deg, #879099, #39707b);padding:1.5rem;border-left:6px solid #4caf50;border-radius:1rem;box-shadow:0 4px 14px rgba(0,0,0,.08)}.trademark-container .steps .step-card h3{margin-bottom:.5rem;font-size:1.2rem}.trademark-container .steps .step-card p{color:#333;font-size:1rem}.trademark-container .advantages ul,.trademark-container .registration-types ul,.trademark-container .classes ul,.trademark-container .faqs ul{margin-top:1rem}.trademark-container .cta{text-align:center;background:#39707b;color:#fff;padding:2rem;border-radius:1.5rem;box-shadow:0 6px 20px rgba(0,0,0,.15)}.trademark-container .cta h2{font-size:2rem}.trademark-container .cta p{font-size:1.1rem;margin-top:.5rem}@media(max-width: 768px){.trademark-container .form-trademark-wrapper{flex-direction:column}.trademark-container .form-trademark-wrapper .form-section,.trademark-container .form-trademark-wrapper .why-trademark{width:100%;margin-bottom:1.5rem}.trademark-container .pricing-docs,.trademark-container .steps{flex-direction:column}}